import Vue from 'vue'
import VueRouter from 'vue-router'

// import Home from '@/views/Home'
// import About from '@/views/About'
// import Item from '@/views/Item'

// import User from './views/User'
// import Profile from './views/User/Profile'
// import Cart from './views/User/Cart'

// import BookChoose from './views/Book/BookChoose'
// import BookBoy from './views/Book/BookBoy'
// import BookGirl from './views/Book/BookGirl'

// import NotFound from '@/views/NotFound'

import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  // scrollBehavior: () => ({ y: 0 }),
  scrollBehavior (to, from, savedPosition) {
    // console.log(savedPosition)
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes: [{
      path: '/',
      name: 'home',
      component: () => import('./views/Home.vue')
    },
    
    {
      path: '/about',
      name: 'about',
      // beforeEnter(to, from, next) {
      //   console.log('beforeEnter');
      //   next();
      // },
      component: import('./views/About.vue')
    },
    {
      // path: '/item/:itemId/:page',
      path: '/item/:itemId',
      name: 'item',
      component: () => import('./views/Item.vue'),
      //props: true,  //r => ({ itemId: Number(r.params.itemId), page: r.params.page })
      //props: {a: 1, b: 2},
      props: r => ({ itemId: Number(r.params.itemId) })
    },
    {
      path: '/user',
      alias: '/member',
      component: () => import('./views/User.vue'),
      meta: { requiresAuth: true },
      children: [
          {
              path: '',
              name: 'user',
              component: () => import('./views/User/Profile.vue'),
          },
          {
              path: 'cart',
              name: 'user-cart',
              component: () => import('./views/User/Cart.vue'),
          }
      ]
    },
    

    {
        path: '/book',
        name: 'book',
        redirect: to => {
            let type = localStorage.getItem('book-type')
            return { name: type || 'book-choose' }
        }
    },
    {
        path: '/book-choose',
        name: 'book-choose',
        component: () => import('./views/Book/BookChoose.vue')
    },
    {
        path: '/book-boy',
        name: 'book-boy',
        component: () => import('./views/Book/BookBoy.vue')
    },
    {
        path: '/book-girl',
        name: 'book-girl',
        component: () => import('./views/Book/BookGirl.vue')
    },

    {
        path: '*',
        component: () => import('./views/NotFound.vue')
    }
    
  ]
});

// const whiteLists = ['home', 'about'];

let isLogin = true;

router.beforeEach((to, from, next) => {
  NProgress.start();
  console.log('全局 - beforeEach');


  console.log(to);
  // 鉴权
  // if (whiteLists.includes(to.name)) {
  //   next();
  // } else {
  //   // router.push({name: 'home'})
  //   next({
  //     name: 'about'
  //   });
  // }

    // if ( to.meta.requiresAuth && !isLogin ) {
    if ( to.matched.some(t=>t.meta.requiresAuth) && !isLogin ) {
      next('/');
    } else {
      next();
    }

})

router.afterEach((to, from, next) => {
  NProgress.done()
})

export default router;
